    export default {
    	data () {
    		return {
                title:'',
                cont:'',
                add:'',
                volume:'',
				aa:''
    		}
    	},
        onLoad(option) {
            this.title = option.title
            this.cont = option.cont

			this.aa=this.cont.replace(/_/g,"=");
            this.add = option.add
            this.volume = option.volume
        }
    }
